code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

/* ================================================================= */

.mainHeader {
  margin-bottom: 16px;
  text-align: center;
  color: #9e0202;
  text-shadow: 0 1px 1px #fff;
  font-size: 32px;
}
